/* MENU */

/* Mobile first layout SmartMenus Core CSS (it's not recommended editing these rules)
   You need this once per page no matter how many menu trees or different themes you use.
-------------------------------------------------------------------------------------------*/

.sm {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  line-height: normal;
  direction: ltr;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  ul,
  li {
    display: block;
    list-style: none;
    margin: 0;
    padding: 0;
    line-height: normal;
    direction: ltr;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
}

.sm-rtl {
  direction: rtl;
  text-align: right;
  ul,
  li {
    direction: rtl;
    text-align: right;
  }
}

.sm {
  > li > {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 0;
      padding: 0;
    }
  }
  ul {
    display: none;
  }
  li {
    position: relative;
  }
  a {
    position: relative;
    display: block;
    &.disabled {
      cursor: not-allowed;
    }
  }
  &:after {
    content: "\00a0";
    display: block;
    height: 0;
    font: 0px/0 serif;
    clear: both;
    visibility: hidden;
    overflow: hidden;
  }
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    &:before,
    &:after {
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
    }
  }
}

.sm-clean {
  background: #eeeeee;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
  a {
    padding: 13px 20px;
    /* make room for the toggle button (sub indicator) */
    padding-right: 58px;
    font-size: 18px;
    font-weight: normal;
    line-height: 24px;
    text-decoration: none;
    &:hover,
    &:focus,
    &:active {
      padding: 13px 20px;
      /* make room for the toggle button (sub indicator) */
      padding-right: 58px;
      font-size: 18px;
      font-weight: normal;
      line-height: 24px;
      text-decoration: none;
    }
    &.current {
      color: #d23600;
    }
    &.disabled {
      color: #bbbbbb;
    }
    span.sub-arrow {
      position: absolute;
      top: 50%;
      margin-top: -17px;
      left: auto;
      right: -45px;
      width: 34px;
      height: 34px;
      overflow: hidden;
      font: bold 16px/34px monospace !important;
      text-align: center;
      text-shadow: none;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      -ms-border-radius: 5px;
      -o-border-radius: 5px;
      border-radius: 5px;
    }
    &.highlighted span.sub-arrow:before {
      display: block;
      content: "-";
    }
  }
  > li {
    &:first-child > {
      a,
      :not(ul) a {
        -webkit-border-radius: 5px 5px 0 0;
        -moz-border-radius: 5px 5px 0 0;
        -ms-border-radius: 5px 5px 0 0;
        -o-border-radius: 5px 5px 0 0;
        border-radius: 5px 5px 0 0;
      }
    }
    &:last-child > {
      a,
      *:not(ul) a {
        -webkit-border-radius: 0 0 5px 5px;
        -moz-border-radius: 0 0 5px 5px;
        -ms-border-radius: 0 0 5px 5px;
        -o-border-radius: 0 0 5px 5px;
        border-radius: 0 0 5px 5px;
      }
      ul {
        -webkit-border-radius: 0 0 5px 5px;
        -moz-border-radius: 0 0 5px 5px;
        -ms-border-radius: 0 0 5px 5px;
        -o-border-radius: 0 0 5px 5px;
        border-radius: 0 0 5px 5px;
        > li:last-child > {
          a,
          *:not(ul) a {
            -webkit-border-radius: 0 0 5px 5px;
            -moz-border-radius: 0 0 5px 5px;
            -ms-border-radius: 0 0 5px 5px;
            -o-border-radius: 0 0 5px 5px;
            border-radius: 0 0 5px 5px;
          }
          ul {
            -webkit-border-radius: 0 0 5px 5px;
            -moz-border-radius: 0 0 5px 5px;
            -ms-border-radius: 0 0 5px 5px;
            -o-border-radius: 0 0 5px 5px;
            border-radius: 0 0 5px 5px;
            > li:last-child > {
              a,
              *:not(ul) a {
                -webkit-border-radius: 0 0 5px 5px;
                -moz-border-radius: 0 0 5px 5px;
                -ms-border-radius: 0 0 5px 5px;
                -o-border-radius: 0 0 5px 5px;
                border-radius: 0 0 5px 5px;
              }
              ul {
                -webkit-border-radius: 0 0 5px 5px;
                -moz-border-radius: 0 0 5px 5px;
                -ms-border-radius: 0 0 5px 5px;
                -o-border-radius: 0 0 5px 5px;
                border-radius: 0 0 5px 5px;
                > li:last-child > {
                  a,
                  *:not(ul) a {
                    -webkit-border-radius: 0 0 5px 5px;
                    -moz-border-radius: 0 0 5px 5px;
                    -ms-border-radius: 0 0 5px 5px;
                    -o-border-radius: 0 0 5px 5px;
                    border-radius: 0 0 5px 5px;
                  }
                  ul {
                    -webkit-border-radius: 0 0 5px 5px;
                    -moz-border-radius: 0 0 5px 5px;
                    -ms-border-radius: 0 0 5px 5px;
                    -o-border-radius: 0 0 5px 5px;
                    border-radius: 0 0 5px 5px;
                    > li:last-child > {
                      a,
                      *:not(ul) a,
                      ul {
                        -webkit-border-radius: 0 0 5px 5px;
                        -moz-border-radius: 0 0 5px 5px;
                        -ms-border-radius: 0 0 5px 5px;
                        -o-border-radius: 0 0 5px 5px;
                        border-radius: 0 0 5px 5px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      a.highlighted,
      *:not(ul) a.highlighted {
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        -ms-border-radius: 0;
        -o-border-radius: 0;
        border-radius: 0;
      }
      ul > li:last-child > {
        a.highlighted,
        *:not(ul) a.highlighted {
          -webkit-border-radius: 0;
          -moz-border-radius: 0;
          -ms-border-radius: 0;
          -o-border-radius: 0;
          border-radius: 0;
        }
        ul > li:last-child > {
          a.highlighted,
          *:not(ul) a.highlighted {
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            -ms-border-radius: 0;
            -o-border-radius: 0;
            border-radius: 0;
          }
          ul > li:last-child > {
            a.highlighted,
            *:not(ul) a.highlighted {
              -webkit-border-radius: 0;
              -moz-border-radius: 0;
              -ms-border-radius: 0;
              -o-border-radius: 0;
              border-radius: 0;
            }
            ul > li:last-child > {
              a.highlighted,
              *:not(ul) a.highlighted {
                -webkit-border-radius: 0;
                -moz-border-radius: 0;
                -ms-border-radius: 0;
                -o-border-radius: 0;
                border-radius: 0;
              }
            }
          }
        }
      }
    }
    &:first-child {
      border-top: 0;
    }
  }
  ul {
    padding-left: 35px;
    a {
      font-size: 16px;
      &:hover,
      &:focus,
      &:active {
        font-size: 16px;
      }
    }
    ul ul {
      a {
        border-left: 24px solid transparent;
        &:hover,
        &:focus,
        &:active {
          border-left: 24px solid transparent;
        }
      }
      ul {
        a {
          border-left: 32px solid transparent;
          &:hover,
          &:focus,
          &:active {
            border-left: 32px solid transparent;
          }
        }
        ul a {
          border-left: 40px solid transparent;
          &:hover,
          &:focus,
          &:active {
            border-left: 40px solid transparent;
          }
        }
      }
    }
  }
}

.main-menu ul {
  width: 12em;
  /* fixed width only please - you can use the "subMenusMinWidth"/"subMenusMaxWidth" script options to override this if you like */
}

.sm-clean a {
  padding-right: 0;
  padding-left: 0;
  &:hover,
  &:focus,
  &:active {
    padding-right: 0;
    padding-left: 0;
  }
}

a.has-submenu {
  display: inline-block;
}

.main-menu.sm-clean {
  background-color: transparent;
  display: inline-block;
}

.sm-clean {
  a {
    color: #ffffff;
    &.current,
    &:hover,
    &:focus,
    &:active,
    &.highlighted {
      color: #c0d3d3;
    }
  }
  ul a {
    &:hover,
    &:focus,
    &:active,
    &.highlighted {
      color: #c0d3d3;
    }
  }
  a span.sub-arrow {
    color: #0697ae;
  }
}

.main-menu.sm-clean .sub-menu li a {
  color: #848484;
  text-transform: uppercase;
  font-size: 14px;
}
