blockquote {
  margin-top: 110px;
  margin-bottom: 60px;
  font-family: "Montserrat", sans-serif, Arial;
  font-size: 22px;
  line-height: 35px;
  color: #b0c2c2;
  width: 395px;
  float: left;
  margin-left: -170px;
  margin-right: 70px;
  &:before {
    content: "\201C";
    font-size: 180px;
    line-height: 0;
    font-family: "Montserrat", sans-serif, Arial;
    vertical-align: -65px;
    color: #0697ae;
    text-align: center;
    display: block;
  }
}

.wrap-blockquote {
  display: inline;
}
address {
  font-size: 32px;
  line-height: 50px;
  font-style: normal;
}
iframe {
  width: 100%;
}
a.button {
  display: inline-block;
  border: 3px solid;
  text-align: center;
  padding: 15px 40px;
  cursor: pointer;
  margin-bottom: 25px;
  vertical-align: middle;
  text-decoration: none;
  &:active {
    position: relative;
    top: 1px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 20px 0;
  &.entry-title a {
    text-decoration: none;
  }
}
h1 {
  font-size: 38px;
  line-height: 38px;
}
h2 {
  font-size: 34px;
  line-height: 34px;
}
h3 {
  font-size: 32px;
  line-height: 32px;
}
h4 {
  font-size: 30px;
}
h5 {
  font-size: 28px;
}
h6 {
  font-size: 26px;
}
em {
  font-style: italic;
}
ol {
  list-style-type: decimal;
}
pre {
  word-wrap: break-word;
}

.single-content,
.page-content {
  table,
  th,
  td {
    border: 1px solid black;
    padding: 10px;
    vertical-align: middle;
  }
}
sup {
  vertical-align: super;
  font-size: smaller;
}
sub {
  vertical-align: sub;
  font-size: smaller;
}
.single-post {
  a::after {
    content: "\f08e";
    font-family: FontAwesome;
    font-size: 70%;
    padding-left: 6px;
  }
  ol {
    display: block;
    list-style-type: decimal;
    margin: 0 0 14px 17px;
  }
  ul {
    display: block;
    list-style-type: disc;
    margin: 0 0 14px 17px;
    ol,
    ul {
      list-style-type: circle;
    }
  }
}

/* Site Data Style */

.logo-holder {
  line-height: 0;
  font-size: 0;
  img {
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: auto;
  }
}

.site-info-holder {
  padding-left: 50px;
  padding-top: 15px;
}

.site-title {
  font-family: "Montserrat", sans-serif, Arial;
  color: #fff;
  font-size: 30px;
  line-height: 38px;
  font-weight: 700;
  padding: 20px 0;
}

.site-description {
  font-family: "Montserrat", sans-serif, Arial;
  color: #848484;
  font-size: 14px;
  line-height: 23px;
  max-width: 200px;
}

.site-content {
  width: calc(100% - 100px);
  margin-left: 100px;
  padding-bottom: 100px;
  position: relative;
  -webkit-transition: margin 0.3s ease;
  -moz-transition: margin 0.3s ease;
  -ms-transition: margin 0.3s ease;
  -o-transition: margin 0.3s ease;
  transition: margin 0.3s ease;
}

body.page-scroll .site-content {
  padding-bottom: 0;
}

body.open .site-content {
  margin-left: 420px;
}

.cat-links {
  a {
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-color: #000000;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  &.active {
     z-index: 2;
     opacity: .9;
     visibility: visible;
  }
}