/* FOOTER */
footer {
  position: absolute;
  bottom: 0px;
}

.footer-info {
  font-family: "Montserrat", sans-serif, Arial;
  padding-left: 50px;
  padding-right: 30px;
  padding-bottom: 60px;
  color: #848484;
  font-size: 10px;
  line-height: 16px;
  letter-spacing: 0;
  a {
    color: #848484;
  }
  .fa-heart {
    color: #d8588c;
    padding-left: 3px;
    padding-right: 2px;
    -webkit-animation: pulse 1s linear infinite;
    -moz-animation: pulse 1s linear infinite;
    -ms-animation: pulse 1s linear infinite;
    animation: pulse 1s infinite;
  }
}

@keyframes "pulse" {
  0% {
    transform: scale(1.1);
  }

  70% {
    transform: scale(0.8, 0.8);
  }

  80% {
    transform: scale(0.9, 0.9);
  }

  100% {
    transform: scale(1.1);
  }
}

@-moz-keyframes pulse {
  0% {
    transform: scale(1.1);
  }

  70% {
    transform: scale(0.8, 0.8);
  }

  80% {
    transform: scale(0.9, 0.9);
  }

  100% {
    transform: scale(1.1);
  }
}

@-webkit-keyframes "pulse" {
  0% {
    transform: scale(1.1);
  }

  70% {
    transform: scale(0.8, 0.8);
  }

  80% {
    transform: scale(0.9, 0.9);
  }

  100% {
    transform: scale(1.1);
  }
}

@-ms-keyframes "pulse" {
  0% {
    transform: scale(1.1);
  }

  70% {
    transform: scale(0.8, 0.8);
  }

  80% {
    transform: scale(0.9, 0.9);
  }

  100% {
    transform: scale(1.1);
  }
}

/* END FOOTER */
