/* COLUMNS */
.one,
.one_half,
.one_third,
.two_third,
.three_fourth,
.one_fourth {
  margin-right: 50px;
  float: left;
  position: relative;
  margin-bottom: 30px;
  img {
    max-width: 100%;
    height: auto;
    max-height: 100%;
    width: auto;
  }
}

.col-inline {
  display: flex;
}

.last {
  margin-right: 0 !important;
  clear: right;
}

.one {
  float: none;
  display: block;
  clear: both;
  margin-right: 0;
}

.one_half {
  width: 50%;
}

.one_third {
  width: 233px;
}

.two_third {
  width: 566px;
}

.one_fourth {
  width: 150px;
}

.three_fourth {
  width: 650px;
}

.team-holder {
  .one {
    margin: 0;
  }
  .one_half {
    width: 50%;
    width: calc(100% / 2);
    margin: 0;
  }
  .one_third {
    width: 33%;
    width: calc(100% / 3);
    margin: 0;
  }
  .one_fourth {
    width: 25%;
    width: calc(100% / 4);
    margin: 0;
  }
}

/* ABOUT PAGE */

.page {
  .page-header-image {
    line-height: 0;
    height: 60vh;
    background-position: center bottom;
    background-size: cover;
    img {
      width: 100%;
      max-width: 100%;
      max-height: 100%;
      height: auto;
    }
  }
  h1.entry-title {
    font-family: "Montserrat", sans-serif, Arial;
    font-size: 22px;
    line-height: 35px;
  }
}

.page-template-default article {
  padding-top: 110px;
}

.team-holder {
  .member {
    line-height: 0;
    font-size: 0;
    img {
      width: 100%;
    }
  }
  .member-info {
    text-align: center;
    background-color: #f1f1f1;
    color: #0697ae;
  }
  .member-name {
    font-family: "Montserrat", sans-serif, Arial;
    font-size: 20px;
    line-height: 28px;
    padding: 40px 60px 0px 60px;
  }
  .member-position {
    font-family: "Montserrat", sans-serif, Arial;
    font-size: 14px;
    line-height: 22px;
    padding: 10px 60px 0px 60px;
  }
  .member-content {
    font-size: 16px;
    line-height: 27px;
    color: #404040;
    padding: 40px 60px 40px 60px;
  }
  .member.one {
    img {
      width: 50%;
      display: inline-block;
    }
    .member-info {
      display: inline-block;
      width: 50%;
      position: absolute;
      height: 100%;
      text-align: left;
    }
  }
}

/* END ABOUT PAGE */
