/* GALERIES */

.gallery-caption {
  font-size: 100%;
}

#galleries-wrapper {
  width: 90%;
  margin: 0 auto;
}

.galleries-holder {
  padding-top: 100px;
  max-width: 100%;
  padding-bottom: 85px;
}

.galleries-item-holder {
  transition: 0.2s filter ease-in-out;
  -webkit-transition: 0.2s -webkit-filter ease-in-out;
}

#instafeed {
  width: 80%;
  text-align: center;
  margin: 0 auto;
  figcaption {
    display: none;
  }
  img {
    max-width: 240px;
    max-height: 240px;
    vertical-align: middle;
  }
  figure {
    display: inline-block;
    margin: 10px;
    width: 240px;
    height: 240px;
    text-align: center;
    line-height: 13;
    background-color: #eee;
  }
}
/* END GALERIES */
