/* Code & Clipboard */
pre {
  margin: 2rem 0;
  position: relative;
  color: #ffffff;
  border: 1px solid #23241f;
  border-radius: 5px;
  -webkit-box-shadow: 0 8px 16px rgba(0, 20, 80, 0.04), 0 4px 16px rgba(0, 0, 0, 0.4);
  box-shadow: 0 8px 16px rgba(0, 20, 80, 0.04), 0 4px 16px rgba(0, 0, 0, 0.4);
  .copy-to-clipboard {
    position: absolute;
    right: 4px;
    top: 4px;
    background-color: #fff;
    color: #ccc;
    border-radius: 2px;
  }
}

.border-radius {
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
}

code {
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  padding: 15px 30px !important;
  cursor: pointer;
}

code + .copy-to-clipboard {
  margin-left: -1px;
  border-left: 0 !important;
  font-size: inherit !important;
  vertical-align: middle;
  height: 21px;
  top: 0;
}

.copy-to-clipboard {
  background-image: url(../images/clippy.svg);
  background-position: 50% 50%;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  width: 27px;
  height: 1.45rem;
  top: -1px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  color: #5e5e5e;
  background-color: #fff7dd;
  margin-left: -0.2rem;
  cursor: pointer;
  border-radius: 0 2px 2px 0;
  margin-bottom: 1px;
}
