/* SINGLE POST */

.single-post {
  article {
    margin: 0 auto;
    text-align: justify;
    overflow: auto;
  }
  h1.entry-title {
    font-family: "Montserrat", sans-serif, Arial;
    margin-bottom: 30px;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    font-size: 48px;
    line-height: 50px;
    padding-top: 120px;
    text-align: left;
    width: 660px;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

.post-info {
  font-family: "Montserrat", sans-serif, Arial;
  padding-bottom: 70px;
}

.entry-content {
  p {
    margin-top: 25px;
  }
}