
table {
    width: 100%;
    margin-bottom: 1rem;
    color: #212529;
    border-collapse: collapse;
    tr,
    tbody,
    thead {
        vertical-align: middle;
        border-color: inherit;
    }
    tbody {
        display: table-row-group;
    }
    thead {
        display: table-header-group;
    }
    td, 
    th {
        padding: .75rem;
        vertical-align: top;
        border-top: 1px solid $color-3;
    }
    tr {
        display: table-row;
    }
    th {
        text-align: inherit;
    }
}