/* RESPONSIVE PART */

@media screen and (max-width: 1650px) {
  .content-1070 {
    width: 1070px;
    max-width: 90%;
  }
  .blog-holder {
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 10%;
    padding-right: 10%;
  }
  .cbp-bislideshow.scroll li {
    width: 55%;
  }
  .page-template-default article {
    padding-top: 70px;
  }
}

@media screen and (max-height: 900px) {
  .social-list {
    font-size: 23px;
    a {
      padding-top: 25px;
    }
  }
}

@media screen and (max-height: 700px) {
  .social-list {
    font-size: 23px;
    a {
      padding-top: 33px;
    }
  }
}

@media screen and (max-width: 1340px) {
  blockquote,
  blockquote.inline-blockquote {
    display: block !important;
    float: none;
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }
}

@media screen and (max-width: 1220px) {
  .one_half {
    width: 48%;
  }
  .one_third {
    width: 30.6%;
  }
  .two_third {
    width: 65.1332%;
  }
  .one_fourth {
    width: 21.8%;
  }
  .three_fourth {
    width: 73.8%;
  }
  .one,
  .one_half,
  .one_third,
  .two_third,
  .three_fourth,
  .one_fourth {
    margin-right: 4%;
  }
  .header-holder {
    padding-top: 85px;
  }
  .header-logo {
    float: none;
    display: block;
    text-align: center;
    margin-bottom: 60px;
  }
  .header-menu {
    float: none;
  }
  .main-menu ul {
    width: 12em;
  }
  .sm-clean a {
    padding-right: 0;
    padding-left: 0;
    &:hover,
    &:focus,
    &:active {
      padding-right: 0;
      padding-left: 0;
    }
  }
  #blog-wrapper {
    margin-top: 300px;
  }
  .featured-post-image {
    top: 0;
    left: 0;
    height: 300px;
    width: 100%;
    position: absolute;
  }
  #blog-wrapper {
    width: 100%;
  }
  .blog-holder {
    padding-top: 50px;
    padding-left: 10%;
    padding-right: 10%;
  }
  .load-more-holder {
    padding-left: 10%;
    padding-right: 10%;
  }
  .featured-post {
    padding-bottom: 15px;
  }
  .team-holder .member.one .member-info {
    position: relative;
    vertical-align: top;
  }
}

@media screen and (max-width: 1020px) {
  .one_half,
  .one_third,
  .one_fourth,
  .two_third,
  .three_fourth {
    width: 100%;
    float: none;
    margin-right: 0;
    margin-left: 0;
  }
  .content-660 {
    width: 660px;
    max-width: 90%;
  }
  .single-post {
    article {
      padding: 0;
    }
    h1.entry-title {
      padding-top: 50px;
    }
  }
  .full-screen-scroll .cbp-bislideshow {
    position: relative;
    width: 100%;
    left: 0;
    z-index: 1;
  }
  .open .full-screen-scroll .cbp-bislideshow {
    left: 0;
  }
  .page-scroll .nicescroll-rails-hr {
    display: none !important;
  }
  .cbp-bislideshow.scroll li {
    position: relative;
    width: 100%;
    height: auto;
    left: 0 !important;
    border-right: 0;
    border-bottom: 10px solid #2d2d2d;
  }
  .full-screen-scroll {
    article {
      opacity: 0.9 !important;
      position: relative;
      padding: 50px 0px;
    }
    h2.entry-title {
      margin-top: 0;
      font-size: 33px;
      line-height: 40px;
    }
  }
  .team-holder .member.one {
    img {
      display: block;
      margin: 0 auto;
      width: 400px;
      max-width: 90%;
    }
    .member-info {
      display: block;
      width: 400px;
      max-width: 90%;
      margin: 0 auto;
      text-align: center;
      position: relative;
    }
  }
  div.contact-form {
    margin-top: 100px;
  }
  .page-header-map #map {
    height: 250px;
  }
  .page-template-default article,
  .blog-holder {
    padding-top: 50px;
  }
  .blog-item-holder.featured-post .entry-content .entry-title {
    padding-top: 0;
  }
}

@media screen and (max-width: 500px) {
  .blog-item-holder.featured-post .entry-content .entry-title {
    font-size: 40px;
    line-height: 43px;
  }
  .sidebar {
    width: 360px;
  }
  .menu-right-part {
    width: 40px;
  }
  #toggle {
    padding: 0;
    height: 40px;
    .menu-line,
    &:before,
    &:after {
      width: 20px;
      left: 10px;
      top: 19px;
      height: 2px;
    }
    &:before {
      -webkit-transform: translate3d(0, -7px, 0);
      transform: translate3d(0, -7px, 0);
    }
    &:after {
      -webkit-transform: translate3d(0, 7px, 0);
      transform: translate3d(0, 7px, 0);
    }
  }
  .social-list {
    font-size: 15px;
    a {
      padding-top: 10px;
    }
  }
  .sidebar.open {
    width: 100%;
  }
  .open .menu-left-part {
    width: calc(100% - 100px);
  }
  .site-content {
    width: calc(100% - 40px);
    margin-left: 40px;
  }
  body.open .site-content {
    z-index: -1;
    margin-left: 40px;
  }
  .search-field {
    height: 40px;
    font-size: 14px;
  }
  .cat-links {
    display: block;
    line-height: 22px;
    margin-top: 10px;
    &:first-child {
      margin-top: 0;
    }
  }
  .published {
    display: block;
    line-height: 22px;
    &:before {
      display: none;
    }
  }
  .cat-links ul li:after {
    display: none;
  }
  .team-holder .member-content {
    padding-left: 5%;
    padding-right: 5%;
  }
  .scroll-top {
    margin: 0 0px;
    .btn {
      width: 32px;
      height: 32px;
      &::after,
      &::before {
          height: 3px;
          width: 14px;
          top: 18px;
      }
      &::before {
          left: 16px;
      }
      &::after {
          left: 10px;
      }
    }
  }
  .col-inline {
    display: block;
  }
}
