/* BLOG */

#blog-wrapper {
  width: 50%;
  margin-left: 0;
  float: left;
}

.blog-holder {
  padding-top: 100px;
  max-width: 470px;
  padding-bottom: 85px;
}

.blog-item-holder {
  transition: 0.2s filter ease-in-out;
  -webkit-transition: 0.2s -webkit-filter ease-in-out;
}

.featured-post {
  padding-bottom: 105px;
}

.blog-item-holder {
  &.featured-post .entry-content .entry-title {
    font-size: 48px;
    line-height: 50px;
    padding-bottom: 40px;
  }
  .entry-content .entry-title {
    a {
      color: #242424;
      &:hover {
        color: #0697ae;
      }
    }
    font-family: "Montserrat", sans-serif, Arial;
    font-size: 30px;
    line-height: 30px;
    padding-top: 35px;
    padding-bottom: 0;
  }
}

.excerpt {
  font-size: 16px;
  line-height: 28px;
  color: #5d5d5d;
}

.read-more {
  background-image: url("../images/arrow.png");
  width: 30px;
  height: 30px;
  background-size: 30px;
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: -11px;
  margin-left: 15px;
}

.load-more-holder {
  font-family: "Montserrat", sans-serif, Arial;
  font-size: 18px;
  font-weight: 700;
  color: #c0d3d3;
  display: block;
  max-width: 470px;
  margin-left: auto;
  margin-right: auto;
}

.more-posts {
  display: block;
  color: #c0d3d3;
  &:hover {
    cursor: pointer;
    color: #aabdbd;
  }
}

.featured-image-holder {
  width: 50%;
  float: right;
}

.featured-post-image {
  position: fixed;
  height: 100%;
  width: 50%;
  width: calc(50% - 50px);
  background-size: cover;
  background-position: center center;
}

.cat-links {
  font-family: "Montserrat", sans-serif, Arial;
  display: inline-block;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 700;
  ul li {
    display: inline-block;
    margin-right: 5px;
    a {
      color: #d8588c;
      font-size: 14px;
    }
    &:after {
      content: ",";
    }
    &:last-child:after {
      display: none;
    }
  }
}

.published {
  font-family: "Montserrat", sans-serif, Arial;
  display: inline-block;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
  color: #aabdbd;
  &:before {
    content: "\2022";
    font-size: 14px;
    display: inline-block;
    margin-right: 10px;
  }
}

.blog .excerpt {
  font-size: 16px;
  line-height: 28px;
  color: #5d5d5d;
}

/* END BLOG */
