.result-container {
  height: 50px;
  display: block;
  background-color: #2d2d2d;
}
.typeahead__cancel-button {
  position: absolute;
  display: none;
  right: 0;
  padding: 0px 10px;
  font-size: 1.4em;
  background-color: #333333;
  color: #ccc;
  border-radius: 50%;
  margin: 8px;
}
.search-field {
  font-family: "Montserrat", sans-serif, Arial;
  width: 100%;
  height: 50px;
  font-size: 18px;
  padding-left: 50px;
  background-color: #2d2d2d;
  color: #5b5b5b;
  border: none;
  &::-webkit-input-placeholder {
    font-family: "Montserrat", sans-serif, Arial;
    color: #5b5b5b;
  }
  &::-moz-placeholder {
    font-family: "Montserrat", sans-serif, Arial;
    color: #5b5b5b;
    opacity: 1;
  }
  &:-ms-input-placeholder {
    font-family: "Montserrat", sans-serif, Arial;
    color: #5b5b5b;
    opacity: 1;
  }
  &:-moz-placeholder {
    font-family: "Montserrat", sans-serif, Arial;
    color: #5b5b5b;
    opacity: 1;
  }
}

// Typeahead.JS
.typeahead__container {
  &.result {
    .typeahead__result {
      display: block;
    }
  }
  &.cancel {

  }
  .typeahead__result {
    position: absolute;
    background-color: #333;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: none;
    ul {
      padding: 10px 20px;
      li {
        a {
          color: $color-3;
        }
      }
    }
  }
}
#form-country_v2 .typeahead__list {
  max-height: 300px;
  overflow-y: auto;
  overflow-x: hidden;
}

.project-jquerytypeahead.page-demo .typeahead__list li a {
  position: relative;
}

.project-jquerytypeahead.page-demo .result-container {
  position: absolute;
  color: #777;
  top: -1.5em;
}

.project-jquerytypeahead.page-demo .typeahead-search li a .flag-chart {
  position: absolute;
  right: 16px;
  top: 10px;
}

.flag-chart {
  position: absolute;
  right: 10px;
  top: 6px;
  width: 30px;
  height: 20px;
  line-height: 14px;
  vertical-align: text-top;
  display: inline-block;
  margin: -1px 3px -1px 0;
  background:url(/assets/jquerytypeahead/img/country_v2.png) no-repeat scroll left top transparent;
}
.flag-united-states {
  background-position: -30px top;
}
.flag-united-kingdom {
  background-position: -60px top;
}
.flag-france {
  background-position: -90px top;
}
.flag-germany {
  background-position: -120px top;
}
.flag-denmark {
  background-position: -150px top;
}
.flag-italy {
  background-position: -180px top;
}
.flag-mexico {
  background-position: -210px top;
}
.flag-brazil {
  background-position: -240px top;
}
.flag-canada {
  background-position: -270px top;
}

