.sidebar {
  position: fixed;
  background-color: #2d2d2d;
  height: 100% !important;
  width: 420px;
  left: -320px;
  -webkit-transition: left 0.3s ease;
  -moz-transition: left 0.3s ease;
  -ms-transition: left 0.3s ease;
  -o-transition: left 0.3s ease;
  transition: left 0.3s ease;
  z-index: 3;
  &.active {
    left: 0;
  }
}

.scroll-top {
  bottom: 35px;
  margin: 0 20px;
  transition: background-color .3s,
     opacity .5s, visibility .5s;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  &.show {
    opacity: .9;
    visibility: visible;
  }
  &:hover {
    cursor: pointer;
  }
  .btn {
    display: inline-block;
    background-color: transparent;
    border: 4px solid #0697ae;
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 50%;
    margin: 0px;
    &:hover {
      cursor: pointer;
      background-color: #47afbf18;
    }
    &::after,
    &::before {
        content: "";
        height: 4px;
        width: 22px;
        background: #0697ae;
        top: 26px;
        position: absolute;
        -webkit-transform-origin: 50% 50%;
        transform-origin: 50% 100%;
        -webkit-transition: -webkit-transform 0.25s;
        transition: background-color .3s, transform 0.25s;
    }
    &::before {
        left: 23px;
        -webkit-transform: rotate3d(0, 0, 1, 45deg);
        transform: rotate3d(0, 0, 1, 45deg);
    }
    &::after {
        left: 13px;
        -webkit-transform: rotate3d(0, 0, 1, -45deg);
        transform: rotate3d(0, 0, 1, -45deg);
    }
    &:hover::after,
    &:hover::before {
        background-color: #fff;
    }
    &:active {
      background-color: #555;
    }
  }
  .fa {
    font-size: 36px;
    width: 32px;
    color: #0697ae;
    &:hover {
      color: #47afbf;
    }
  }
}

#header-main-menu {
  font-family: "Montserrat", sans-serif, Arial;
  padding-left: 50px;
  padding-top: 20px;
}

.menu-left-part {
  width: 320px;
  position: absolute;
  background: rgb(79, 82, 174);;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 3;
}

.menu-right-part {
  float: right;
  width: 100px;
  min-height: 100%;
  background-color: rgb(46,49,146);
}

#toggle {
  height: 60px;
  padding: 20px 0;
  position: relative;
  background-color: rgb(79, 82, 174);
  &:hover {
    cursor: pointer;
  }
  &:before,
  &:after {
    content: "";
    height: 4px;
    width: 26px;
    background: #fff;
    left: 36px;
    position: absolute;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transition: -webkit-transform 0.25s;
    transition: transform 0.25s;
    top: 48px;
  }
  &:before {
    -webkit-transform: translate3d(0, -8px, 0);
    transform: translate3d(0, -8px, 0);
  }
  &:after {
    -webkit-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
  }
  &.on {
    &:before {
      -webkit-transform: rotate3d(0, 0, 1, 45deg);
      transform: rotate3d(0, 0, 1, 45deg);
    }
    &:after {
      -webkit-transform: rotate3d(0, 0, 1, -45deg);
      transform: rotate3d(0, 0, 1, -45deg);
    }
    .menu-line {
      opacity: 0;
    }
  }
  .menu-line {
    width: 26px;
    height: 4px;
    background: #fff;
    left: 36px;
    position: absolute;
    -webkit-transition: opacity 0.25s;
    -moz-transition: opacity 0.25s;
    -ms-transition: opacity 0.25s;
    -o-transition: opacity 0.25s;
    transition: opacity 0.25s;
    top: 48px;
  }
}

.social-list {
  text-align: center;
  color: #5b5b5b;
  font-size: 30px;
  a {
    padding-top: 80px;
    display: block;
    color: #fff;
    &:hover {
      color: #797979;
    }
  }
}
